<template>
  <v-app id="iris">
    <v-content class="bg-food">
      <v-container fluid>
        <v-layout align-center justify-center>
          <v-flex xs12 sm6 md4>
            <Avatar :action="action" />
            <v-card class="mx-auto pa-3" color="#2d68c4" dark>
              <v-card-text class="headline text-center text-white  mb-5" v-model="message">
                {{ message }}
              </v-card-text>
            </v-card>
            <v-text-field class="mt-5 mb-2"
              label="URL da imagem"
              placeholder="Cole aqui a url da imagem"
              v-model="urlImage"
            ></v-text-field>
            <v-btn small color="primary" block v-on:click="getFood">Enviar</v-btn>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import Avatar from "./components/Avatar.vue";
import axios from "axios";

export default {
  name: "App",
  components: {
    Avatar
  },
  data: () => {
    return {
      message: '',
      urlImage: '',
      count: 0,  
      action: 0
    };
  },
  methods: {
    typeWriter: function () {
      if (this.count < this.current.length) {
        this.message += this.current.charAt(this.count);
        this.count++;
        setTimeout(this.typeWriter, 100);
      } else {
        this.action = "default";
      }
    },
    getFood: function() {
      axios.get('http://localhost:5000/?image_url='+this.urlImage)
      .then(function (response) {
        // handle success
        console.log(response);
      })
      .catch(function (error) {
        // handle error
        console.log(error);
      })
      .then(function () {
        // always executed
      });
    }
  },
  mounted(){
    this.current = 'Olá, meu nome é Iris! Percebi que não gostou da nossa comida.';
    this.action = this.current.length * 100;
    this.typeWriter();
  }
};
</script>

